import Vue from 'vue';
import VueRouter from 'vue-router';

const IndexPage = () => import("@/pages/Index");
const WelcomePage = () => import("@/pages/Welcome");
const ServerPage = () => import("@/pages/Server");
const EditPage = () => import("@/pages/EditPage");

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    redirect: "/wel",
  },
  {
    path: '/wel',
    name: 'WelcomePage',
    component: WelcomePage
  },
  {
    path: '/index',
    name: 'Index',
    component: IndexPage
  },
  {
    path: '/server',
    name: 'ServerPage',
    component: ServerPage
  },
  {
    path: '/edit',
    name: 'EditPage',
    component: EditPage
  },

]

export default new VueRouter({
  // mode: "history",
  routes
});
